body {
    font-family: 'Montserrat'; 
    font-size: 14px;
    a {
        text-decoration: none;
    }
    ul {
        list-style-type: none;
    }
}

.wrap {
    max-width: 1160px;
    margin: 0 auto;
    padding-left: 54px;
    padding-right: 54px;
}

.content-wrap {
    max-width: 1160px;
    margin: 0 auto;
    padding-left: 140px;
    padding-right: 140px;
}

.content {
    display: flex;
    flex-direction: column-reverse;
    flex-wrap: wrap-reverse;
    background-image: url(../img/1.png);
    background-position: -42px -128px;
    background-repeat: no-repeat;
    background-size: 106%;
    height: 830px;
}

.content__text {
    max-width: 297px;
    text-align: right;
    line-height: 23px;
    margin-bottom: 7px;
    //color: rgba(31, 32, 65, 0.75);
    color: rgb(255, 0, 0);
}

.sign-up-in-content {
    background-image: url(../img/image_4.1.png);
    background-position: center -70px;
    width: 1440px;
    height: 684px;
}

//MEDIA
@media (max-width: 1055px) {
    header.header {
        .wrap {
            padding-right: 15px;
        }
    }
}