.reviews-room__circle {
    position:relative;
    width:120px;
    height:120px;
    margin:0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.quarter {
    position:absolute;
    width:50%;
    height:50%;
    /*transition:background-color 0.2s ease-in-out;*/
}
